<template>
<div class="inner">
  <div class="board_table">
    <h1>식단기록</h1>
    <p>오늘 먹은 것들을 기록해보세요.</p>
    <div id="app" class="container-fluid text-center">
    <p>{{ msg }}</p>

    <div class="col">
      <div v-if="error" class="alert alert-danger" @click="error = !error">
        <strong>Error:</strong> 내용을 입력하세요!
      </div>
      <form @submit.prevent="addTask">
        <div class="input-group mb-3">
          <input type="text" class="form-control form-control2" placeholder="뭘 먹었나요?" v-model="taskName">
          <div class="input-group-append">
            <button v-if="update" class="btn" type="submit">
              <i class="fa fa-pencil" aria-hidden="true"></i>
            </button>
            <button v-else class="btn" type="submit">
              <i class="fa fa-plus" aria-hidden="true"></i>
            </button>
          </div>
        </div>
      </form>

      <button v-if="deleteMultiple" class="btn btn-danger mb-3" @click="deleteMulti">내용 삭제하기</button>

      <ul class="list-group">
        <li v-for="(task_name,index) in tasks" :key="index" class="list-group-item list-group-item-info">
          <div class="row">
            <div class="col-2">
              <input type="checkbox" :checked="task_name['check']" @change="changeCheck" :id="task_name['index']" class="form-control form-control3">
            </div>
            <div class="col" @click="loadData(index)">{{ task_name['task'] }}</div>
          </div>
        </li>
      </ul>
    </div>
  </div>
  </div>
</div>
</template>

<style>
  @font-face {
    font-family: 'Cafe24SsurroundAir';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24SsurroundAir.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
  .inner {text-align:center;}
  .board_table {border-radius: 10px;padding: 10px 20px;width:85%;display:inline-block;
  margin:10px 0;
  border:1px solid #dee1e6;
  font-family: "Cafe24SsurroundAir";font-size:12px;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);}
  #bootstrap-overrides h1 {font-family: 'Cafe24Ssurround';color: #ffeeee;background: #f24182;font-size:20px;
  padding:5px 0;border-radius:12px 12px 12px 12px;margin-bottom:20px}
  img {width:100%;}
  /*TODO*/
  #bootstrap-overrides .input-group .btn {background: #f24182;border:1px solid #f24182}
  #bootstrap-overrides .form-control {
    font-family: "Cafe24SsurroundAir";
    font-size: 12px;
    line-height: 15px;
    font-weight: 400;}
  #bootstrap-overrides .form-control2 {height:38px;font-size: 14px;}
  #bootstrap-overrides .form-control3 {padding:0.4rem 0.4rem;height: .2em;}
  #bootstrap-overrides .col-2 {width: 10px;}
  #bootstrap-overrides .btn:focus{ 	
    border: none;
    outline:none
  }
  .list-group-item {padding:.5rem 1rem;}
  .fa-plus {color:#fff}
  div.col { 
    font-family: "Cafe24SsurroundAir";
    font-size: 0.8rem;}
  #bootstrap-overrides .list-group-item-info {
  color: #333!important;
  background-color: #ffeeee!important;
  }
  #bootstrap-overrides .btn-danger {font-size:14px;background:#f24182;color:#fff;font-weight:bold}
</style>
<script>
export default {
  name: 'app',
  data () {
    return {
      title: 'VueDo List',
      taskName: "",
      tasks: [
        {"index": 0, "task": '차돌박이 짬뽕', "check": false},
        {"index": 1, "task": '아이스아메리카노', "check": false},
        {"index": 2, "task": '초코스콘', "check": false},
      ],
      error: false,
      update: false,
      updateIndex: null,
      ids: [],
      deleteMultiple: false
    }
  },
  updated: function(){
    console.log("UPDATED----");
    this.tasks.forEach((task) => {
      console.log(task.task,  "--", task.check);
    });
    console.log("----");
  },
  methods: {
    deleteMulti: function(e){
      this.tasks.forEach((task) => {
        console.log(task.task, task.check);
      });
      console.log("----");
      var ids = [];
      this.tasks.forEach((task) => {
        if(task.check == true){
          ids.push(task.index)
        }
      });
      ids.forEach(id => {
        this.tasks = this.tasks.filter(function(task) {
          return task['index'] != id;
        });
      });
      this.deleteMultiple = false;
      this.tasks.forEach((task) => {
        console.log("DELETE:", task.task, task.check);
      });
    },

    changeCheck: function(e){
      this.tasks[e.target.id]['check'] = e.target.checked
      var count = 0
      this.tasks.forEach((task) => {
        if(task.check == true){
          count = count + 1
        }
      })
      this.deleteMultiple = (count > 0) ? true : false
      console.log("changeCheck");
      this.tasks.forEach((task) => {
        console.log(task.task, task.check);
      });
    },

    loadData: function(index){
      console.log(this.tasks)
      this.taskName = this.tasks[index]['task']
      this.updateIndex = index
      this.update=true
    },

    addTask: function(){
      if(this.update == true){

        this.update = false
        this.tasks[this.updateIndex]['task'] = this.taskName

      }
      else{
        var theIndex = this.tasks.length;
        if(this.taskName != ''){
          this.tasks.push({
            "index": theIndex,
            "task": this.taskName,
            "check": false
            })
        }
        else{
          this.error = true
        }

      }

      this.taskName = ''
    },
  }
}
</script>


