<template>
<div class="inner">
  <div class="board_table">
  <h1>칼로리 계산기</h1>
  <p>수치를 정확히 입력해주세요.</p>
  <div>
  </div>
    <div class="calc-outer-wrap">
    <div id="form-error" style="display:none;"></div>
    <form class="calc-form" id="calc-form">
      <div class="form-fields">

        <div class="field-outer">
          <label for="distance" class="top-label">나이:</label>
          <div class="field-items">
            <div class="field field-large">         
              <input type="text" name="age" id="age" inputmode="numeric" pattern="[0-9]*">
            </div><!--  /.field-large -->
            
            <div class="field field-medium">
              <select name="gender">
                <option value="Female">여자</option>
                <option value="Male">남자</option>
                      
              </select>
              
            </div><!--  /.field-small -->
          </div><!--  /.field-items -->
        </div><!--  /.field-outer -->

        <div class="field-outer">
          <label for="height" class="top-label">신장:</label>
          <div class="field-items">
            <div class="field">
              <input type="text" name="height_ft" id="height-ft" inputmode="numeric" pattern="[0-9]*">
              <label for="height-ft">feet</label>
            </div><!--  /.field -->
            <div class="field">
              <input type="text" name="height_in" id="height-in" inputmode="numeric" pattern="[0-9]*">
              <label for="height-in">inches</label>
            </div><!--  /.field -->
          <div class="field">
              <input type="text" name="height_cm" id="height-cm" inputmode="numeric" pattern="[0-9]*">
              <label for="height-cm">centimeters</label>
            </div><!--  /.field -->        
          </div><!--  /.field-items -->
        </div><!--  /.field-outer -->

        <div class="field-outer">
          <label for="weight" class="top-label">몸무게:</label>
          <div class="field-items">
            <div class="field field-large">
              <input type="text" name="weight" id="weight" inputmode="numeric" pattern="[0-9]*">
            </div><!--  /.field-large -->
            <div class="field field-small">
              <select name="weight_unit">
                <option value="kg">kg</option>
                <option value="lb">lb</option>
              </select>
            </div><!--  /.field-small -->
          </div><!--  /.field-items -->
        </div><!--  /.field-outer -->
      <div class="btn-wrap field-outer">
        <button class="form-submit">칼로리 계산하기</button>
      </div>
      </div><!--  /.form fields -->

    </form>
    <div id="results" style="display:none;"></div>
    </div><!--  /.calc-outer-wrap -->
  </div>
</div>
</template>

<style>
  @font-face {
    font-family: 'Cafe24SsurroundAir';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24SsurroundAir.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
  .inner {text-align:center;}
  .board_table {border-radius: 10px;padding: 10px 20px;width:85%;display:inline-block;
  margin:10px 0;
  border:1px solid #dee1e6;
  font-family: "Cafe24SsurroundAir";font-size:12px;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);}
  #bootstrap-overrides h1 {font-family: 'Cafe24Ssurround';color: #ffeeee;background: #f24182;font-size:20px;
  padding:5px 0;border-radius:12px 12px 12px 12px;margin-bottom:20px}
  .button_Y {background: #f24182;border:0px;color:#2c3e50;right:0px;font-weight:bold;color: #ffeeee;
  font-size:14px}
  .fa-heart {color: #f24182;margin-left:5px}
  .fa-edit {margin-right:5px}
  .event_banner {margin:20px 0}
  img {width:100%;}
  th {border-top:2px solid #f24182;background:#ffeeee}
  /*계산기*/
    $border-radius : 0.1875rem;
  $primary: #9951FF;
  $primary-dark: #803ede;
  $secondary: #59e7ed;
  $secondary-dark: darken($secondary, 10);
  * {box-sizing: border-box;}

  html, body {
    margin: 0;
    padding: 0;
  }
  .calc-outer-wrap {
    margin: 0 auto;
    background: #f3f3f3;
    padding: 0.625rem;
    font-size: 1rem;
    line-height: 1.3;
    font-family: Helvetica Neue,Helvetica, Arial, sans-serif;
  }

  .form-fields {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: top;
  }
  .field-outer {
    width: 98%;
  }
  label,
  input,
  select {
    font-size: 1rem;
    line-height: 1.3;
    display: block;
  }
  input,
  select {
    height: 1.75rem;
    border: 1px solid #bbb;
    width: 100%;
    background: #fff;
    padding: 0 0.325rem;
    border-radius : $border-radius;
    transition: box-shadow 200ms ease-in-out;
    box-shadow: inset 0 0 0 0 $secondary;
    -webkit-appearance: none;
    -moz-appearance: none;
          appearance: none;

    &:focus {
      outline: 0;
      box-shadow: inset 0 -3px 0 -1px $secondary;
      border-radius: $border-radius $border-radius 1px 1px;
    }
  }
  select {
    background: #fff url("data:image/svg+xml;charset=UTF-8,%3csvg width='8px' height='14px' viewBox='0 0 8 17' xmlns='http://www.w3.org/2000/svg' fill='%238A8A8A'%3e%3cpolygon fill='%238A8A8A' points='4 17 0 10 8 10'%3e%3c/polygon%3e%3cpolygon fill='%238A8A8A' points='4 0 0 7 8 7'%3e%3c/polygon%3e%3c/svg%3e") 94% center no-repeat;
    &:-moz-focusring {
      color: transparent;
      text-shadow: 0 0 0 #000;
    }
    &::-ms-expand {
      display: none;
    } 
  }
  .field-items {
    display: flex;
    justify-content: space-between;

    label {
      font-size: 0.75rem;
      color: #999;
    }
  }
  .field {
    display: inline-block;
    width: 32%;

    + .field {
      margin-left: 3px;
    }

  }
  .field-large {
    flex: 1;
    width: 67%;
  }
  .field-small {
    width: 3em;
  }
  .top-label {
    margin-top:10px;
    font-weight: bold;
    font-size: 0.875rem;
    line-height: 1.4;
    display: block;
  }

  .btn-wrap {
    align-self: flex-end;
  }
  .form-submit {
    margin-top:20px;
    margin-bottom:10px;
    background: #f24182;
    display: block;
    border: 0;
    width: 100%;
    font-size: 1rem;
    border-radius: $border-radius;
    height: 1.75rem;
    font-weight: 600;
    color:#fff;
    box-shadow: 1px 1px 1px 0 rgba(0,0,0,0.15);
    cursor: pointer;

    &:hover,
    &:focus {
      background: $secondary-dark;
      box-shadow: 2px 2px 3px 0 rgba(0,0,0,0.3);
      outline: 0;
    }

    &:active {
      box-shadow: none;
    }
  }

  #form-error {
    color: #fff;
    margin-bottom: 0.5rem;
    text-align:center;
    color: #c1000b;
    background-color: #ffd5d5;
    padding: 3px;
    font-size: 0.875rem;
  }

  #results {
    position: relative;
      width: 100%;
      height: 110px;
      display: flex;
      justify-content: center;
      align-items: center;
    p {
      margin: 0;
    }
    
    #rs {
      position: absolute;
      bottom: 10px;
      right: 10px;
      font-size: 0.875rem;
      color: #aaa;
      &:hover {
        color: $primary;
      }
    }
  }
</style>
<script src="../script.babel"></script>
<script>
  import BoardVisual from "@/components/BoardVisual";

  import data from "@/data";
    let items = data.Content.sort((a, b) => {
        return b.content_id - a.content_id;
      }); // 내림차순
      const user_name='운영자'
    
  export default {
    name: "Board",
   
    
  }
</script>


