<template>
<div class="inner">
  <div class="board_table">
  <h1>리뷰</h1>
  <p>이달의 베스트 리뷰</p>
  <div>
    <b-carousel 
      id="carousel-fade"
      style="text-shadow: 0px 0px 2px #000"
      fade
      indicators
      img-width="1024"
      img-height="480"
    >
      <b-carousel-slide
        img-src="../static/01.jpg"
      ></b-carousel-slide>
      <b-carousel-slide
        img-src="../static/02.jpg"
      ></b-carousel-slide>
      <b-carousel-slide
        img-src="../static/03.jpg"
      ></b-carousel-slide>
      <b-carousel-slide
        img-src="../static/04.jpg"
      ></b-carousel-slide>
      </b-carousel>
    </div>
    <div class="event_banner">
      <img src="../assets/banner_1.png" alt="이벤트">
    </div>
    <p>또시락 회원님들의 소중한 후기<i class="fas fa-heart"></i></p>
    <b-table 
      striped
      hover
      :items="items"      
      :fields="fields"
      @row-clicked="rowClick"
    ></b-table>
    <b-button class="button_Y" @click="writeContent"><i class="fas fa-edit"></i>리뷰작성</b-button>
  </div>
</div>
</template>

<style>
  @font-face {
    font-family: 'Cafe24SsurroundAir';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24SsurroundAir.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
  .inner {text-align:center;}
  .board_table {border-radius: 10px;padding: 10px 20px;width:85%;display:inline-block;
  margin:10px 0;
  border:1px solid #dee1e6;
  font-family: "Cafe24SsurroundAir";font-size:12px;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);}
  #bootstrap-overrides h1 {font-family: 'Cafe24Ssurround';color: #ffeeee;background: #f24182;font-size:20px;
  padding:5px 0;border-radius:12px 12px 12px 12px;margin-bottom:20px}
  #bootstrap-overrides .button_Y {background: #f24182!important;border:none;color:#2c3e50;right:0px;font-weight:bold;color: #ffeeee;
  font-size:14px}
  #bootstrap-overrides .btn-secondary {background: #f24182;border:none;}
  .fa-heart {color: #f24182;margin-left:5px}
  .fa-edit {margin-right:5px}
  .event_banner {margin:20px 0}
  img {width:100%;}
  #bootstrap-overrides th {border-top:2px solid #f24182;background:#ffeeee}
</style>

<script>
  import BoardVisual from "@/components/BoardVisual";

  import data from "@/data";
    let items = data.Content.sort((a, b) => {
        return b.content_id - a.content_id;
      }); // 내림차순
      const user_name='운영자'
    
  export default {
    name: "Board",
    data() {
    
      return {
      
        // bootstrap 'b-table' 필드 설정
        fields: [
          {
            key: "content_id",
            label: ""
          },
          {
            key: "title",
            label: "제목"
          },
          {
            key: "user_name",
            label: "닉네임"
          }
        ],
        items: items
      };
    },
    methods: {
    
      rowClick(items) {
        this.$router.push({
          path: `/detail/${items.content_id}`
        });
        
      },
    
      writeContent() {
        this.$router.push({
          path: `/create`
        });
        
      }
    },
    computed: {
      rows() {
          return this.items;
      }
    },
    
  }
</script>


