<template>
  <div class="board_table">
    <h1>리뷰</h1>
    <p>또시락 회원님들의 소중한 후기<i class="fas fa-heart"></i></p>
    <b-card>
      <div class="content-detail-content-info">
        <div class="content-detail-content-info-left">
          <div class="content-detail-content-info-left-subject">{{title}}</div>
        </div>
        <div class="content-detail-content-info-right">
          <div class="content-detail-content-info-right-user">닉네임: {{user}}</div>
          <div class="content-detail-content-info-right-created">등록일: {{created}}</div>
        </div>
      </div>
      <div class="content-detail-content">{{context}}</div>
      <div class="content-detail-button">
        <b-button variant="primary" @click="updateData" class="button_Y">수정</b-button>&nbsp;
        <b-button variant="success" @click="deleteData" class="button_N">삭제</b-button>
      </div>
     
    </b-card>
  </div>
</template>

<script>
import data from "@/data";

export default {
  name: "BoardDetail",
  data() {
    const contentId = Number(this.$route.params.contentId);
    const contentData = data.Content.filter(
      contentItem => contentItem.content_id === contentId
    )[0];
    return {
      contentId: contentId,
      title: contentData.title,
      context: contentData.context,
      user: contentData.user_name,
      created: contentData.created_at
    };
  },
  methods: {
    deleteData() {
      const content_index = data.Content.findIndex(
        contentItem => contentItem.content_id === this.contentId
      );
      data.Content.splice(content_index, 1); // 데이터 삭제
     
      localStorage.setItem('items', JSON.stringify(data.Content)); //로컬에 JSON 

     this.$router.push({
        path: "/"
      });
    },
    updateData() {
    
      this.$router.push({
        path: `/create/${this.contentId}`
      });
    }
  }
};
</script>
<style scoped>
@font-face {
  font-family: 'Cafe24SsurroundAir';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24SsurroundAir.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
.card-body {border:0px}
.board_table {border-radius: 10px;padding: 10px 20px;width:85%;display:inline-block;
  margin:10px 0;
  border:1px solid #dee1e6;
  font-family: "Cafe24SsurroundAir";font-size:12px;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1)
  }
  #bootstrap-overrides h1 {font-family: 'Cafe24Ssurround';color: #ffeeee;background: #f24182;font-size:20px;
  padding:5px 0;border-radius:12px 12px 12px 12px;;margin-bottom:20px}
  .button_Y {background: #f24182;border:0px;color:#2c3e50;right:0px;font-weight:bold;color: #ffeeee;
  font-size:14px}
  .button_N {background: #f4a0bf;border:0px;color:#2c3e50;right:0px;font-weight:bold;color: #ffeeee;
  font-size:14px}
.content-detail-content-info {
  display: flex;
  justify-content: space-between;
}
.content-detail-content-info-left {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.content-detail-content-info-right {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}
.content-detail-content {
  margin-top: 1rem;
  padding-top: 1rem;
  min-height: 220px;
}
.content-detail-button {
  margin-top: 1rem;
  padding: 0.5rem;
}
.fa-heart {color: #f24182;margin-left:5px}
</style>


