<template>
  <div class="board_table">
    <h1>또건강 TIP</h1>
    <div class="board_table_in">
      <mdb-accordion :panes="panes">
      </mdb-accordion>
    </div>
  </div>
</template>
<script>
  import { mdbAccordion } from 'mdbvue';

  export default {
    components: {
      mdbAccordion
    },
    data() {
      return {
        panes: [
          {
            title: '설탕 많은 음료 피하기',
            content: `단 음료는 비만의 주범 중 하나이다. 우리의 두뇌는 고체 음식과 같은 방식으로 액체 설탕에서 칼로리를 측정하지 않는다. 따라서 탄산음료나 청량음료를 마시면 더 많은 칼로리를 섭취하게 된다. 단 음료는 비만, 당뇨병, 심장병 등 여러 건강 문제와 밀접한 관련이 있다.`
          },
          {
            title: '견과류를 먹는다',
            content: `지방이 많기는 하지만 견과류는 영양가가 높고 건강하다. 마그네슘, 비타민 E, 섬유질 및 기타 다양한 영양소가 풍부하게 들어있다. 연구에 따르면 견과류는 체중감량, 당뇨병과 심장병 해결에 도움이 될 수 있다고 한다.`
          },
          {
            title: '가공된 정크푸드를 피한다',
            content: `가공된 정크푸드는 쾌락중추를 자극하도록 설계되었기 때문에 뇌를 과식하도록 속이고 심지어 음식 중독을 조장하기도 한다. 일반적으로 섬유질, 단백질, 미량 영양소는 적고 건강에 해로운 성분은 많다.`
          },
                  {
            title: '커피를 두려워하지 말자',
            content: `아직 논쟁 중이지만 커피는 건강한 음료로 점점 인식되어가고 있다. 커피, 특히 그린 커피에는 항산화 물질이 많이 들어 있다. 연구에 따르면 커피 섭취는 장수, 당뇨병, 파킨슨병, 알츠하이머병 등 여러 질병의 위험 감소와 관련이 있다고 한다.`
          },
                  {
            title: '기름진 생선을 먹는다',
            content: `생선은 고품질 단백질과 건강한 지방의 훌륭한 공급원이다. 생선에 많은 오메가-3 지방산의 건강 효과는 널리 알려져 있다.`
          },
                  {
            title: '충분한 수면을 취한다',
            content: `충분한 수면은 매우 중요하다. 잠이 부족하면 인슐린 저항성을 유발하고, 식욕 호르몬을 방해하며, 신체적·정신적 능력이 떨어진다.`
          },
                  {
            title: '섬유질로 장 건강을 유지한다',
            content: `유산균이라고도 하는 장내 박테리아는 건강에 매우 중요하다. 장내 세균총의 균형이 깨지면 비만을 포함한 여러 가지 만성 질환에 걸리기 쉽다. 장 건강을 개선하는 좋은 방법은 요구르트와 김치 같은 유산균 함유 식품을 많이 먹고, 프로바이오틱스 보충제와 좋은 섬유질을 많이 섭취하는 것이다.`
          },
                  {
            title: '식사 전에 물을 조금 마신다',
            content: `충분한 물을 마시면 많은 이점이 있다. 놀랍게도 칼로리 소모량을 늘릴 수 있다. 연구에 따르면 1~1.5시간 동안 신진대사를 24~30% 증가시킬 수 있었다고 한다. 식사 직전에 물을 마시면 가장 큰 효과를 얻을 수 있다고 하니 참고하자. 매 식사 30분 전에 물 500ml(2컵 정도)를 마시면 체중 감소가 44% 상승했다고 한다.`
          },
                  {
            title: '고기를 타도록 익히지 않는다',
            content: `고기가 너무 익거나 타면 암을 유발하는 해로운 화합물이 형성될 수도 있다.`
          },
                  {
            title: '비타민 D를 더 많이 섭취한다',
            content: `햇빛은 비타민 D의 훌륭한 공급원이다. 그러나 겨울철이나 특히 코로나19 사태 이후로 집안에서 생활하는 시간이 길어지면서 충분한 햇빛에 노출되지 않는 경우가 많다. 비타민 D 보충제가 좋은 대안이다. 비타민 D는 매우 다양한 효능이 있는데, 요즈음 면역기능을 증진하는 물질로 더 많은 조명을 받고 있다.`
          },
        ],
      }
    }
  }
</script>

<style>
  @font-face {
    font-family: 'Cafe24SsurroundAir';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24SsurroundAir.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
  .inner {text-align:center;}
  .board_table {border-radius: 10px;padding: 10px 20px;width:85%;display:inline-block;
  margin:10px 0;
  border:1px solid #dee1e6;
  font-family: "Cafe24SsurroundAir";font-size:12px;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);}
  #bootstrap-overrides h1 {font-family: 'Cafe24Ssurround';color: #ffeeee;background: #f24182;font-size:20px;
  padding:5px 0;border-radius:12px 12px 12px 12px;margin-bottom:20px}
  h4 {font-size:16px;}
  img {width:100%;}
  a {color: #f24182;text-decoration: none;font-size:12px}
  a:hover {color: #f24182; font-weight:bold}
  input:focus {
    outline: none;
  }
  textarea:focus {
    outline: none;
  }
  /*리스트*/
  #bootstrap-overrides .card-header:first-child {
    border-radius: 0px;
  }
  #bootstrap-overrides .card  {
    border-bottom:none;
    border: 1px solid rgba(0,0,0,.125);
    border-bottom: none}
  #bootstrap-overrides .card-header {
    padding: 0.2rem 0.5rem;
    margin-bottom: 0;
    background-color: rgba(0, 0, 0, 0.03);
    box-shadow: 0px rgba(0, 0, 0, 0);
    border: rgba(0, 0, 0, 0)
  }
  #bootstrap-overrides .accordion .card {
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.0), 0 2px 10px 0 rgba(0, 0, 0, 0.0);
  }
  #bootstrap-overrides .accordion .card .card-body {
    border-top: 1px solid #eee;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.0), 0 2px 10px 0 rgba(0, 0, 0, 0.0);
  }
  #bootstrap-overrides .btn-link {color: #f24182; text-decoration: none}
  #bootstrap-overrides .btn {color: #f24182; text-decoration: none; background-color:transparent;}
  #bootstrap-overrides .card h5 {height:35px}
</style>


