<template>
  <div>
  <b-carousel 
    id="carousel-fade"
    style="text-shadow: 0px 0px 2px #000"
    fade
    indicators
    img-width="1024"
    img-height="480"
  >
    <b-carousel-slide
      caption="우리 강아지 간식에는 도그밀"
      img-src="../static/01.jpg"
    ></b-carousel-slide>
    <b-carousel-slide
      caption="이달의 포토상 <춘배>"
      img-src="../static/02.jpg"
    ></b-carousel-slide>
    <b-carousel-slide
      caption="이달의 포토상 <봉달이>"
      img-src="../static/03.jpg"
    ></b-carousel-slide>
    <b-carousel-slide
      caption="이달의 포토상 <영희>"
      img-src="../static/04.jpg"
    ></b-carousel-slide>
  </b-carousel>
</div>
</template>

<style>
  .board_table {border-radius: 10px;width:85%;margin:0 auto;
  font-family: "Cafe24SsurroundAi";font-size:14px;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);}
</style>

<script>
  export default {
    data() {
      return {
        slide: 0,
        sliding: null
      }
    },
    methods: {
      
    }
  }
</script>


